/* Фундоментальные Блоки */

.page_theme {
  font: 14px / 2.14 "Open Sans", sans-serif;
  color: rgba(var(--f75, 38, 38, 38), 1);
  background: #fafafa;
}

.layout__inner {
  position: relative;
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.layout__wrap {
  max-width: 720px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.footer__inner {
  display: flex;
  flex-wrap: wrap;
  padding: 15px 0;
}

.footer__iconline {
  width: 100%;
  display: flex;
}

.footer__icon-heart {
  margin-right: 15px;
}

.footer__icon-comment {
  margin-right: 15px;
}

.footer__icon-share {
  margin-right: 15px;
}

.footer__icon-flag {
  margin-left: auto;
  margin-right: 0;
}

.footer__likeline {
  width: 100%;
  display: flex;
  align-items: center;
}

.footer__ava {
  width: 26px;
  height: 26px;
  border-radius: 50%;
}

.footer__text {
  margin-left: 15px;
}

.img {
  display: block;
  max-width: 100%;
  height: auto;
}

.link {
  display: inline;
  text-decoration: none;
  color: rgba(var(--f75, 38, 38, 38), 1);
  font-weight: bold;
}

.link:hover {
  text-decoration: underline;
  cursor: pointer;
}

.deco.deco_b {
  font-weight: bold;
}

