.content__inner {
  padding: 15px 0;
}

.post__inner {
  background-color: white;
  padding: 25px;
}

.topbar__inner {
  display: flex;
}

.topbar__avabox {
  width: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.topbar__ava {
  width: 36px;
  height: 36px;
  border-radius: 50%;
}

.topbar__about {
  line-height: 20px;
  margin-left: 15px;
}

.topbar__dotsbox {
  margin-left: auto;
  margin-right: 0;
  display: flex;
  align-items: center;
  position: relative;
}

* {
  box-sizing: border-box;
}

html {
  height: 100%;
  overflow-y: scroll;
}

body {
  height: 100%;
  -webkit-overflow-scrolling: touch;
}